import { InjectedComponentSet, styled } from '@topwrite/common';
import Markdown from './markdown';
import { ComponentProps, createElement, ElementType } from 'react';
import useArticle from '@/lib/use-article';
import useFile from '@/lib/use-file';

interface ArticleProps extends Omit<ComponentProps<typeof Markdown>, 'children'> {
    className?: string;
    container?: ElementType;
}

export default function Article({ className, header, footer, container = 'div', ...props }: ArticleProps) {

    const file = useFile();
    const article = useArticle();

    if (header === undefined && article) {
        header = <Title>{article.title}</Title>;
    }

    header = <>
        <InjectedComponentSet role={'page:article:before'} />
        {header}
    </>;

    footer = <>
        {footer}
        <InjectedComponentSet role={'page:article:after'} />
    </>;

    const children = <>
        <Markdown header={header} footer={footer} {...props}>
            {file.content}
        </Markdown>
    </>;

    return createElement(container, { className }, children);
}

const Title = styled.h1`
  border-bottom: 1px solid #eee;
  font-size: 2em;
  line-height: 1.2;
  padding: 0 0 .5em;
  margin: 0 0 1em;
  font-weight: 400;
`;
